import { Component, Input } from '@angular/core';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  len = 1;
  title = 'app';
  items = [];
  newItem = "";
   
  pushItem (newAccount: {name: string, id: number}) {
    if (this.newItem != "") {
      this.items.push({name: this.newItem, id: this.len});
      this.len ++;
      this.newItem = ""
      console.log(this.items);
    }
  }

  removeItem = function(index) {
    this.items.splice(index, 1);
    this.len --;
    console.log(this.items);
  }

  compareById() {
      this.items = this.items.sort((a, b) => a.id < b.id ? -1 : a.id > b.id ? 1 : 0);
      console.log(this.items);
  }

  compareByName() {
    this.items = this.items.sort((a, b) => a.name < b.name ? -1 : a.name > b.name ? 1 : 0);
    console.log(this.items);
  }

}





